.card-area {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card {
    padding: 2rem;
    margin: 0 1rem;
    background-color: #D9D9D9;
    border-radius: 2rem;
}

.card-text {
    font-family: "Lora", sans-serif;
    font-size: 3rem;
}

.captionSmall {
    font-family: "Lora", sans-serif;
    color: #92A8D1
;
}

@media (max-width: 600px) {
    .card {
        padding: 1rem;
        margin: 0 0.25rem;
        border-radius: 1rem;
    }

    .card-text {
        font-size: 1rem;
    }

    .captionSmall {
        font-size: 0.6rem;
    }
}